//IntroCover 
        .Contact-Intro {
            width: 100%;
            height: 100vh;
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            // opacity: 0;
            z-index: 600;
        }
            .Contact-Intro-Wrap {
                width: 100%;
                height: 100%;
                position: relative;
            }  

                .Contact-IntroSlide {
                    position: absolute;
                    width: 50%;
                    height: 100%;
                    top: 0;
                    bottom: 0;
                    background-color: rgb(240, 240, 240);
                    transition: all;
                    z-index: 1;
                }
                    .Contact-IntroSlideLeft {
                        left: -100%;
                    }

                    .Contact-IntroSlideRight {
                        right: -100%;
                    }


//Contact
.Contact {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
}

    .Contact-Wrapper {
        width: 100%;
        width: 100%;
        position: relative;
        padding: 0 10%;

    }

        .Contact-Title {
            margin-top: 100px;
            font-size: 75px;
            font-weight: 900;
            font-family: aktiv-grotesk-condensed, sans-serif;
            text-transform: uppercase;
            transition: all .5s ease-in-out;
        }

            .Contact-Subtitle {
                font-size: 55px;
                font-weight: 200;
                font-family: aktiv-grotesk-condensed, sans-serif;
                text-transform: uppercase;
                margin-bottom: 80px;
                transition: all .5s ease-in-out;
            }

                .Contact-Content {
                    width: 100%;
                    padding-left: 10%;
                    margin-bottom: 80px;
                }

                    .Contact-Section-Title {
                        font-size: 40px;
                        font-weight: 900;
                        font-family: aktiv-grotesk-condensed, sans-serif;
                        text-transform: uppercase;
                        transition: font-size .5s ease-in-out;
                    }

                        .Contact-Section-Text {
                            font-size: 16px;
                            font-weight: 300;
                            margin-bottom: 20px;
                        }

                        .Contact-Link {
                            padding-right: 15px;
                        }
                            .Contact-Link:hover {
                                font-weight: 600;
                            }

                    .FooterContact {
                        position: absolute;
                        bottom: 0;
                        padding-left: 10%;
                    }



@media screen and (max-height: 700px){
    .FooterContact {
        position: relative;
        padding: 0;
    }
}

@media screen and (max-width:500px) {
    .Contact-Title {
        font-size: 50px;
    }
    .Contact-Subtitle {
        font-size: 32px;
    }
    .Contact-Section-Title {
        font-size: 24px;
    }
    .Contact-Content {
        padding-left: 5%;
    }
    .Contact-Link {
        padding-left: 0;
        display: block;
    }
}